import React, { useEffect, useState } from "react"
import { withApolloClient } from "../apollo/apolloSetup"
import { gql } from '@apollo/client'

const GET_NYC_WEATHER = gql`
  query {
    getCityByName(name: "New York") {
      name
      weather {
        clouds {
          humidity
        }
        summary {
          title,
          description,
          icon,
        }
        temperature {
          actual
        }
        wind {
          speed
        }
      }
    }
  }
`

// styles
const pageStyles = {
  color: "#232129",
  padding: 96,
  fontFamily: "-apple-system, Roboto, sans-serif, serif",
}
const headingStyles = {
  marginTop: 0,
  marginBottom: 64,
  maxWidth: 320,
}
const headingAccentStyles = {
  color: "#663399",
}
const paragraphStyles = {
  marginBottom: 48,
}

const IndexPage = (props) => {
  const [nycWeather, setNycWeater] = useState(null)

  useEffect(() => {
    //I want to test the client prop for another project
    props.client.query({
      query: GET_NYC_WEATHER,
    }).then((response) => {
      if (!response.data) { throw Error(response.errors[0].message); }
      return response.data;
    }).then(
      data => setNycWeater(data.getCityByName)
    ).catch(setNycWeater)
  },[])

  console.log(nycWeather)

  return (
    <main style={pageStyles}>
      <title>Home Page</title>
      <h1 style={headingStyles}>
        Gatsby v3, Apollo v3 GraphQL Test
      </h1>
      <p style={paragraphStyles}>
        I've been having a lot of trouble getting Gatsby v3 and Apollo v3 to work together. This is a test site for me to iron out the deploy issues.
      </p>
      <p>
        This app using a free <a href="https://graphql-weather-api.herokuapp.com/" target="_blank" rel='noopener noreferrer'>weather GraphQL API</a> as a proof-of-concept.
      </p>

      {nycWeather && <DisplayWeather data={nycWeather}/>}

      <br/><br/>

      <img
        alt="Gatsby G Logo"
        src="data:image/svg+xml,%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 2a10 10 0 110 20 10 10 0 010-20zm0 2c-3.73 0-6.86 2.55-7.75 6L14 19.75c3.45-.89 6-4.02 6-7.75h-5.25v1.5h3.45a6.37 6.37 0 01-3.89 4.44L6.06 9.69C7 7.31 9.3 5.63 12 5.63c2.13 0 4 1.04 5.18 2.65l1.23-1.06A7.959 7.959 0 0012 4zm-8 8a8 8 0 008 8c.04 0 .09 0-8-8z' fill='%23639'/%3E%3C/svg%3E"
      />
    </main>
  )
}

export default withApolloClient(IndexPage)


const DisplayWeather = (props) => {
  if(props.data instanceof Error) {
    return (
      <section>
        <b>Error: </b> {props.data.message}
      </section>
    )
  }

  return (
    <section>
      <h3>{props.data.name}</h3>
      <p><b>Humidity: </b> {props.data.weather.clouds.humidity}</p>
      <p><b>Summary: </b> {props.data.weather.summary.title}</p>
      <p><b>Temperature: </b> {props.data.weather.temperature.actual}</p>
      <p><b>Wind: </b> {props.data.weather.wind.speed}</p>
    </section>
  )
}